.hero {
    padding-top: 250px;
    padding-top: 160px;
    min-height: 800px;
    background: var(--dark-color);

    &__container {
        @include flex-v-center;
    }

    &__content {
        max-width: 516px;

        @include high-tablet {
            max-width: none;
        }
    }

    &__image {
        transform: translateX(167px);

        @include high-tablet {
            display: none;
        }
    }

    &__title {
        width: 516px;
        margin: 0;
        margin-bottom: 50px;
        font-weight: 600;
        font-size: 70px;
        line-height: 160%;
        text-transform: uppercase;
        color: var(--light-color);

        @include mobile {
            font-size: 30px;
        }

        &-text {
            display: block;
            max-width: 90%;
            font-weight: 400;
            font-size: 20px;
            line-height: 160%;
            color: var(--light-color);
            text-transform: initial;

            @include mobile {
                font-size: 16px;
                max-width: 300px;
            }
        }    
    }

    @include high-tablet {
        padding-bottom: 100px;
        min-height: auto;
    }

    @include mobile {
        padding-bottom: 45px;
    }
}